@import "../../../styles/colors";
@import "../../../styles/fonts";

.notebook-popout {
  position: relative;
  height: 100vh;
  overflow: none;
  width: 60%;
  margin: auto;
  padding-left: 150px;
  // background: yellow;
  .Progress-side-bar {
    width: 100px;
    height: 80%;
    background-color: #173461;
    position: absolute;
    right: 1%;
    border-radius: 10px;
    top: 8%;
  }
  .popout-content {
    height: auto;
    padding: 36px;
    overflow-y: none;
    // background-color: red;
    margin-left: 0%;
    // width: 80%;
    // width: 80%;
    .popout-header {
      h2 {
        display: inline-block;
        padding-bottom: 9px;
        margin-bottom: 10px;
        border-bottom: 8px solid #173461;
        font-weight: 700;
        line-height: 34px;
        font-size: 42px;
        letter-spacing: 2px;
        color: $charcoal1;
      }
    }

    .popout-body {
      // margin-top: 16px;
      // min-height: calc(100% - 151px);
      height: auto;
      // margin-left: 0px;
      margin-top: 10%;
      // background-color: red;
      width: 100%;
      // margin: 1px auto;
      // background-color: #1475d4;
      // .videoWrapper {
      //   position: relative;
      //   height: 0;
      //   padding-top: 25px;
      //   padding-bottom: 56.25%; /* 16:9 */
      // }
      // .videoWrapper iframe {
      //   position: absolute;
      //   top: 0;
      //   left: 0;
      //   width: 100%;
      //   height: 100%;
      // }
      .section-header{
        font-size: 43px;
        text-align: left;
      }
    }
  }

  .popout-footer {
    // position: absolute;
    // right: 0;
    // bottom: 10;
    width: 100%;
    height: 10vh;
    display: grid;
    // background-color: gray;
    grid-template-columns: auto auto;
    // justify-content: flex-start;
    position: absolute;
    grid-gap: 10px;
    // bottom: 0;
    // background-color: red;
    // margin-top: 100px;
    // margin-left: 50px;
    z-index: 2;
    border-radius: 10px;
  }
  .sectionsButtons{
    margin-top: 1%;
    display: grid;
    width: 400px;
    // position: absolute;
    // background-color: #1475d4;
    // bottom: 0;
    grid-template-columns: auto auto;
    .popout-btn {
      width: 180px;
      height: 60px;
      font-size: 16px;
      border: none;
      border-radius: 50px;
      background-color: #1475d4;
      margin-left: 0%;
      color: white;
      cursor: pointer;
    }
  }
}
.Progress {
  width: 80%;
  height: 30px;
  background-color: white;
  margin: auto;
  position: absolute;
  top: 25%;
  left: 5%;
  div {
    div {
      background-color: #3d75d4 !important;
    }
  }
}
.barCompleted {
  background-color: lightblue;
  width: 80%;
}

@media screen and (max-width: 480px) {
  .notebook-popout{
    width: 100%;
    padding-left: 0px;
    margin: 5% auto;
    .popout-btn{
      width: 120px;
      height: 40px;
      border-radius: 20px;
      padding: -10px;
      margin-bottom: 10px;
    }
  }
  .notebook-popout .popout-content .popout-header h2 {
    width: 100%;
  
    font-size: 28px;
    // margin-top: 0%;
    margin-left: 1px;
    
  }

  .iconOpen{
    width: 50px;
    height: 60px;
    position: absolute;
    left: 85%;
    top: 1%;
  }
 
  .popout-footer{
    margin-bottom: -20%;

  }
}

@media screen and (max-height: 480px) {
  .notebook-popout{
    overflow: scroll;
    height: auto;
    width: 100%;
    // margin-top: 0%;
    padding-left: 0px;
  }
  .notebook-popout .popout-content .popout-header h2{
    font-size: 28px;
    margin-top: 10%;
  }
  .iconOpen{
    width: 50px;
    height: 60px;
    margin-left: 92%;
  }
  .notebook-popout .popout-footer .popout-btn{
    width: 150px;
    padding: -10px;
  }
 
}

@media screen and (min-width: 1680px) {
  .notebook-popout {
    margin-left: 20%;
    padding-left: 0px;
  }
}