.role {
  width: 500px;
  height: 300px;
  margin-top: 5%;
  // background-color: rgb(231, 231, 231);
  border-radius: 10px;
}

.Button {
  width: 40%;
  height: 50%;
  // background-color: #0b1e40;
  border-radius: 20px;
  // color: white;
  margin: auto;
}

.role-btn {
  width: 180px;
  height: 60px;
  font-size: 16px;
  border: none;
  border-radius: 6px;
  background-color: #d8d8d8;
  color: black;

  cursor: pointer;
}
.Intro {
  width: 90%;
  // border: 2px solid;
  margin-top: 1%;
  // border-radius: 10px;
  text-align: left;
  p {
    // background-color: brown;
    width: 100%;
    // height: 400px;
    margin: auto;
    font-size: 25px;
  }
}
.InformationData {
  width: 90%;
  /* margin: 5% auto; */
  height: 60vh;
  padding: 10px;
  overflow: scroll;
  /* border: 2px solid lightgray; */
  // border-radius: 10px;
  p {
    text-align: left;
    font-size: 17px;
    // background-color: cadetblue;
    padding-left: 10px;
  }
  // overflow: scroll;
  // border: 2px solid lightgray;
  // border-radius: 10px;
    .table {
      font-family: arial, sans-serif;
      border-collapse: collapse;
      border: none!important;
      width: 100%;
      box-sizing: unset;
      font-size: 20px;
      tr{
        height: 50px;

      }
      tr:nth-child(1){
        font-weight: bold;
      }
      tr:nth-child(1){
        border: none;
      }
      td, th{
        border: none;
      }
      tr:nth-child(even){
        background-color: #F3F3F3;
      }
      tr:nth-child(10){
        font-weight: bold;
      }
    }
}
.ul {
  text-align: left;
}


caption {
  width: 200%;
  /* border: 1px solid; */
  height: 30px;
  font-weight: bold;
  text-align: left;
  font-size: 25px;
}
tr:nth-child(11) {
  background-color: #dddddd;
  font-weight: bold;
}
td,
th {
  border: 1px solid black;
  text-align: left;
  padding: 8px;
}

.pdfApp {
  // width: 80%;
  // height: 100vh;
  // background-color: lightblue;
  // padding: 100px;
  margin: auto;
}
.roleCard {
  background-color: #173461;
  display: grid;
  grid-template-columns: 30% auto;
  grid-gap: 20px;
  width: 90%;
  height: 25vh;
  border-radius: 20px;
  padding: 10px;
  color: white;
  div{
    // background-color: #dddddd;
    height: auto;
    margin-top: auto;
    margin-bottom: auto;
    // margin: 1% auto;
    // text-align: left;
    img {
      margin-top: auto;
      margin-bottom: auto;
      width: 70%;
    }
    button {
      width: auto;
      height: 50px;
      border-radius: 30px;
      background-color: #173461;
      color: white;
      font-size: 16px;
      padding: 10px;
      margin-right: 4%;
      border: 1px solid white;
    }
  }
}
@media screen and (min-width: 1680px) {
  .roleCard{
    width: 70%;
  }
  .InformationData {
    width: 70%;
  }
}
@media screen and (max-width: 480px) {
  .Intro {
    width: 100%;
    margin: 25% auto;

    p {
      // background-color: brown;
      width: auto;
      // height: 400px;
      margin: auto;
      font-size: 15px;
      text-align: left;
    }
  }
  .InformationData {
    width: 100%;
  }
  .pdfApp {
    padding: 0px;
    height: 700px;
    overflow-x: scroll;
    .roleCard{
      width: 90%;
      div:nth-child(1){
        img{
          width: 100%;
        }
      }
      div:nth-child(2){
        h2{
          font-size: small;
        }
        p{
          font-size: small;
        }
        button{
          font-size: small;
          height: 35px;
          margin-bottom: 2%;
        }
      }
    }
  }
  .role {
    width: 100%;
    height: auto;
    .grid-container {
      width: 100%;
      height: auto;
      // background-color: #0b1e40;
      padding: 0px;
      div{
        width: 100%;
        .grid-item {
          width: 90%;
          height: 120px;
          font-size: 10px;
          img {
            width: 80%;
          }
        }
      }
    }
  }
}
@media screen and (max-height: 480px) {
  .Intro {
    width: 100%;
    display: grid;
    height: auto;
    grid-template-columns: auto auto;
    h1 {
      margin-top: -10%;
    }
    p {
      // background-color: brown;
      width: 90%;
      // height: 400px;
      margin: auto;
      font-size: 15px;
    }
  }
  .InformationData {
    margin: 5% auto;
    width: 80%;
  }
  .pdfApp {
    padding: 0px;
  }
  .role {
    width: 80%;
    height: auto;
    .grid-container {
      width: 100%;
      height: auto;
      // margin: auto;
      // background-color: #0b1e40;
      .grid-item {
        width: 90%;
        height: 150px;
        font-size: 15px;

        img {
          width: 90%;
        }
      }
    }
  }
}
.grid-container {
  display: grid;
  grid-template-columns: auto auto;
  padding: 10px;
  div{
    width: 200px;
    p{
      text-align: center;
    }
    .grid-item {
      background-color: lightgray;
      // padding: 20px;
      font-size: 30px;
      height: 200px;
      border-radius: 10%;
      width: 200px;
      margin: 1% auto;
      text-align: center;
      border: none;
      img {
        width: 180px;
        border-radius: 10px;
      }
  }
  }
}

.grid-item:hover {
  -ms-transform: scale(1.1); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(1.1);
}
