.Resources {
  margin-top: 1%;
  display: grid;
  // background-color: #343434;
  grid-template-columns: auto auto auto;
  margin-bottom: 8%;
  .take-away{
    width: 300px;
    height: 300px;
    div:first-of-type{
      // border: 1px solid;
      border-radius: 50%;
      width: 200px;
      height: 200px;
      margin: 10% auto;
      background-color: #3D75D3;
      
    }
    p{
      font-size: 100px;
      color: white;
      padding-top: 40px;
      margin: 10% auto;
    }
    // img{
      // width: 100px;
      // margin:20% auto;
    // }
  }
}
@media screen and (max-width: 480px) {
  .Resources{
    margin: 5% auto;
    display: grid;
    grid-template-columns: auto;
    .take-away {
      width: 100%;
      height: auto;
    }
    table thead {
      display: none;
    }
    table td {
      display: flex;
    }
  }
}
@media screen and (max-height: 480px) {
  .Resources {
    width: 100%;
    margin: 0% auto;
    height: auto;
    margin-bottom: 11%;
    .take-away{
      width: auto;
      height: 250px;
      div{
        p{
          font-size: 100px;
        }
      }
    }
  }
}